@import '../../App.scss';

.App-page {
    $page-header-height: 35vh;

    &-header {
        background-image: url('../../asset/image/company_bg.jpg');
        background-size: cover;
        background-position: center;
        box-shadow: inset;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        height: $page-header-height;
        margin: 0;
        box-shadow: 100vw 30vh rgba(#000000, 0.7) inset;

        $menu-height: 6vh;

        &-head {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: absolute;
            z-index: 2;
            top: 5%;

            @include mobile {
                height: 5vh;
            }
            @include pc {
                height: 10vh;
            }

            &-logo {
                @include mobile {
                    width: 30%;
                    height: 5vh;
                }
                @include pc {
                    width: 15%;
                    height: 7vh;
                }
                margin: 0 5%;                
                cursor: pointer;
            }

            &-menu {
                position: absolute;
                @include mobile {
                    width: 7%;
                    right: 8%;
                    height: 3vh;
                }
                @include pc {
                    width: 2%;
                    right: 6%;
                    height: 4vh;
                }  
                margin: 1.5vh 0;
                display: inline-block;
                transition: all 0.4s;
                box-sizing: border-box;
                cursor: pointer;

                p {
                    width: 100%;
                    color: #797979 !important;
                    text-align: center;
                    position: absolute;
                    box-sizing: border-box;
                    top: 90%;
                    font-weight: bold;
                    font-size: 100% !important;

                    @include mobile {
                        font-size: 80% !important;
                    }
                }

                span {
                    position: absolute;
                    height: 0.5vh;
                    background-color: #797979;
                    border-radius: 4px;
                    transition: all 0.4s;
                    box-sizing: border-box;
                    left: 0;

                    &:nth-of-type(1) {
                        top: 0;
                        width: 100%;
                    }

                    &:nth-of-type(2) {
                        top: 35%;
                        width: 80%;
                    }

                    &:nth-of-type(3) {
                        top: 70%;
                        width: 100%;
                    }
                }
            }
        }

        &-context {
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
            z-index: 1;
        }

        &-menu {
            width: 100%;
            height: $menu-height;
            position: absolute;
            background-color: rgba(#000000, 0.4);
            top: 35vh - $menu-height;

            &-area {
                width: 100%;
                text-align: left;
                list-style: none;
                margin: 0;
                padding: 0;
                display: flex;

                li {
                    display: inline-block;
                    margin: 0;
                    padding: 0;
                    cursor: pointer;
                    border-right: 1px solid rgba(#FFFFFF, 0.5);
                    position: relative;
                    font-size: 90%;

                    ul {
                        background-color: rgba(#000000, 0.4);
                        width: 100%;
                        margin: 0;
                        padding: 0;
                        top: 0;
                        left: 0;
                        position: relative;
                        z-index: 2;
    
                        li {
                            width: 100%;
                            display: block;
                            font-size: 100%;;
                            color: #FFFFFF;
                            border-right: none;
                            

                            p:after { 
                                content: none;
                            }

                            &:hover {
                                width: 100%;
                                display: block;
                                background-color: rgba(#000000, 0.6);
                            }
                        }
                    }
                }
            }

            &-home {
                width: 5%;
                height: $menu-height;
                line-height: $menu-height;
                padding: 0 1vw !important;
                text-align: center;

                &-image {
                    height: $menu-height * 0.6;
                    margin: $menu-height * 0.2 auto;
                }

                @include mobile {
                    display: none !important;
                }
            }

            &-other {
                width: 20%;
                height: $menu-height;
                line-height: $menu-height;

                @include mobile {
                    width: 40%;
                }

                p {
                    margin: 0;
                    padding: 0;
                    padding-left: 3vw;
                    display: block;
                    position: relative;

                    &:after {
                        display: block;
                        content: '';
                        width: 5px;
                        height: 5px;
                        border-right: 2px solid #fff;
                        border-bottom: 2px solid #fff;
                        transform: rotate(45deg) translateY(-50%);
                        position: absolute;
                        left: 90%;
                        top: 50%;
                        margin-top: -2px;
                    }
                }                
            }
        }

        &-info {
            font-weight: bold;
            font-size: 150% !important;
            border-bottom: 3px solid #FFFFFF;
            width: 10%;
            padding: 1vh 0;
            margin: 0 auto !important;

            @include mobile {
                font-size: 100% !important;
                width: 20%;
            }
        }

        &-title {
            font-weight: bold;
            font-size: 400% !important;
            margin: 1vh 0 0.5vh !important;

            @include mobile {
                font-size: 200% !important;
            }
        }

        p {
            margin: 0;
            font-size: 120%;
            color: #FFFFFF;

            @include mobile {
                font-size: 100%;
            }
        }
    }

    &-main {    
        width: 100%;

        &-menu {
            width: 50%;
            height: 5vh;
            line-height: 5vh;
            margin: 0 auto 2vh;
            display: flex;
            background-color: #f1f1f1;

            @include mobile {
                width: 80%;
                height: 4vh;
                line-height: 4vh;
            }

            div {
                flex: 1;
                cursor: pointer;
            }

            .selected {
                background-color: #797979;
                color: white;
            }
            
        }

        &-title {
            &-area {
                width: 80%;
                margin: 5vh auto;

                &-title {
                    font-size: 250%;
                    font-weight: bold;
                    margin: 2vh 0;
                }

                &-context {
                    font-size: 120%;
                    margin: 0;

                    &:after {
                        display: block;
                        content: '';
                        width: 10%;
                        height: 5px;
                        background: #ed2024;
                        margin: 2vh auto 0;
                    }
                }
            }
        }

        &-organization {
            display: flex;
            flex-direction: column;
            justify-content: center;

            &-image {
                width: 60%;
                margin: 2vh auto;

                @include mobile {
                    width: 95%;
                    margin: 5vh auto;
                }
            }

            &-title {
                text-align: left;
                width: 80%;
                margin: 1vh auto;
                font-weight: 700;
            }

            &-table {
                width: 80%;
                margin: 0 auto 5vh;
                border-collapse: collapse;
                box-sizing: border-box;

                @include mobile {
                    width: 95%;
                }

                tr {

                    &:last-of-type {
                        background-color: #e4e4e4;
                        font-weight: 700;
                    }
                }

                th {
                    background-color: #b4b4b4;
                    padding: 1vh 0;
                    width: 14%;
                }

                td {
                    padding: 1vh 0;
                    width: 14%;
                }
            }

            &-second {
                width: 80%;
                margin: 0 auto 1vh;
                border-collapse: collapse;
                box-sizing: border-box;

                tr {
                    &:nth-of-type(1) {
                        background-color: #e4e4e4;
                        font-weight: 600;
                    }
                }

                th {
                    background-color: #b4b4b4;
                    padding: 1vh 0;
                }

                td {
                    padding: 1vh 0;
                }
            }
        }

        &-map {
            width: 100%;
            margin: 0;
            padding: 0;
            display: block;

            img {
                width: 30%;
                height: 35vh;
                float: left;
            }

            &-area {
                width: 100%;

                div {
                    float: left;
                }
            }

            &-context {
                @include mobile {
                    width: 100%;
                }
                @include pc {
                    width: 100% - 40%;
                }
                height: 20vh;
                display: flex;
                flex-direction: column;
                justify-content: center;
                position: relative;
                text-align: left;
                padding: 4% 0 0 2vw;

                ul {
                    list-style: none;

                    li {
                        margin: 4vh 0;

                        div {
                            width: 5%;
                            margin-right: 4vw;
                        }
                    }
                }
            }

            &-info {
                @include mobile {
                    width: 80%;
                    height: 30vh;
                }
                @include pc {
                    width: 30%;
                    height: 30vh;
                }
                padding-top: 4%;
                margin-bottom: 2%;
                background-color: #f1f1f1;
                flex-direction: column;
                justify-content: center;

                p {
                    margin: 0;
                    padding: 0 2vw;
                    text-align: left;

                    @include mobile {
                        padding-left: 8vw;
                    }

                    &:last-of-type {
                        font-size: 300%;
                        margin: 0 0 2vh;
                        position: relative;
                        font-weight: bold;

                        @include mobile {
                            font-size: 250%;
                        }

                        &:after {
                            content: "";
                            display: block;
                            width: 60%;
                            height: 4px;
                            background: #d51c29;
                            position: absolute;
                            left: 5%;
                            bottom: 0;

                            @include mobile {
                                width: 40%;
                            }
                        }
                    }
                }

                ul {
                    list-style: none;
                    margin: 0;
                    padding: 0;

                    li {
                        margin: 0;
                        padding: 1vh 2vw;
                        float: left;
                        width: 100%;
                        text-align: left;
                    }
                }
            }
        }

        &-award {
            width: 80%;
            margin: 0 auto;

            ul {
                list-style: none;
                width: 100%;
                margin: 0;
                padding: 0;
                display: grid;
                grid-template-columns: repeat(4, 1fr);

                li {
                    font-weight: bold;
                    margin-bottom: 3vh;

                    img {
                        width: 70%;
                        border: 10px groove #c2c2c2;
                    }
                }
            }
        }

        &-overview {
            width: 90%;
            margin: 0 auto;

            &-title {
                width: 80%;
                margin: 0 auto 1vh;
                text-align: left;
                font-weight: 700;
                font-size: 25px;
            }

            &-quality {
                width: 80%;
                margin: 0 auto 3vh;

                img {
                    width: 80%;
                    margin: 0 auto;
                }
            }

            &-div {
                width: 80%;
                margin: 0 auto 3vh;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                place-items: center;

                div {
                    width: 70%;
                    height: 20vh;
                    padding: 5vh 1vw;
                    margin: 3vh 0;
                    border: 1px solid #ff4949;
                    line-height: 180%;

                    img {
                        width: 10%;
                    }
                }
                

                &-title {
                    font-weight: 700;
                    width: 50%;
                    height: 5vh;
                    line-height: 5vh;
                    margin: 0 auto;
                    font-size: 18px;
                }
            }

            &-board {
                font-weight: 800;
                font-size: 180% !important;
                float: left;

                @include mobile {
                    font-size: 120% !important;
                }
            }

            &-sign {
                width: 15% !important;
                margin: 0 1vw !important;
                float: left;

                @include mobile {
                    width: 50% !important;
                    margin: 0 0 3vh !important;
                }
            }

            &-context {
                width:55%;
                text-align: left;
                padding: 0 2%;
                @include mobile {
                    width: 90%;
                    margin: 0 auto 5vh;
                } 
                @include pc {
                    float: left;
                    width: 55%;
                }

                &-image {
                    @include mobile {
                        width: 90%;
                        margin: 0 auto 0;
                    } 
                    @include pc {
                        float: left;
                        width: 40%;
                        margin: 10vh 0 0;
                    }   
                }

                p {
                    font-size: 100%;
                    line-height: 200%;
                    margin: 0;
                    margin-bottom: 4vh;

                    &:first-of-type {
                        font-size: 250%;
                        font-weight: bold;
                        margin: 2vh 0;
                    }
                }
            }
        }

        &-history {
            width: 90%;
            margin: 0 auto;

            div {
                margin: 0;
                padding: 0;

                &:first-of-type {
                    @include pc {
                        width: 30%;
                        float: left;
                        padding: 10vh 0;
                    }

                    @include mobile {
                        width: 80%;
                        padding: 5vh 0;
                    }
                    background-color: #f1f1f1;
                    margin: 0 0 2vh;

                    p {
                        margin: 0;
                        padding: 0 2vw;
                        text-align: left;
    
                        &:nth-of-type(2) {
                            font-size: 300%;
                            margin: 0 0 2vh;
                            position: relative;
                            font-weight: bold;
    
                            &:after {
                                content: "";
                                display: block;
                                width: 60%;
                                height: 4px;
                                background: #d51c29;
                                position: absolute;
                                left: 5%;
                                bottom: 0;
                            }
                        }
                    }
                }

                &:last-of-type {
                    @include pc {
                        float: left;
                        width: 70%;
                    }

                    @include mobile {
                        width: 90%;
                    }

                    dl {
                        margin: 0;
                        padding: 0;
                        box-sizing: border-box;

                        @include mobile {
                            font-size: 90%;
                        }

                        dt {
                            float: left;
                            width: 28%;
                            margin: 1vh 0;
                            font-weight: bold;

                            @include mobile {
                                padding-right: 3%;
                            }
                        }
                      
                        dd {
                            float: left;
                            width: 65%;
                            margin: 1vh 0;
                            padding: 0;
                            padding-left: 3%;
                            border-left: 1px solid #797979;

                            p {
                                margin: 0;
                                margin-bottom: 2vh;
                                padding: 0;
                                text-align: left;
                            }
                        }
                    }
                }
            }
        }

        &-board {
            width: 90%;
            margin: 0 auto;

            table {
                width: 100%;
                margin: 0 0 5vh;
                border-top: 2px solid #d51c29;
                border-collapse: collapse;

                tr {
                    &:first-of-type {
                        background-color: #f1f1f1;
                    }
                    border-bottom: 1px solid #797979;

                    th {
                        padding: 1.5vh 0;
                        font-size: 105%;
                        @include mobile {
                            display: none;
                        }
                    }  

                    td {
                        padding: 1.5vh 0;
                        &:first-of-type {
                            width: 10%;
                        }

                        &:nth-of-type(2) {
                            width: 60%;
                            cursor: pointer;
                        }

                        &:nth-of-type(3) {
                            width: 10%;
                        }

                        &:last-of-type {
                            width: 20%; 
                        }

                        @include mobile {
                            &:first-of-type {
                                display: none;
                            }
    
                            &:nth-of-type(2) {
                                width: 70%;
                                cursor: pointer;
                            }
    
                            &:nth-of-type(3) {
                                width: 10%;
                            }
    
                            &:last-of-type {
                                width: 20%; 
                            }
                        }
                    }
                }
            }

            div {
                p {
                    display: inline-block;
                    padding: 1vh 1vw;
                    background-color: #f1f1f1;
                    border: 1px solid #797979;
                    cursor: pointer;
                }
            }
        }

        &-process {
            width: 80%;
            margin: 0 auto;

            p {
                font-weight: bold;
                font-size: 150%;
                margin: 0;
                padding: 0;
                text-align: left;
            }

            ul {
                margin: 0;
                padding: 0;
                margin-left: 2%;
                text-align: left;

                li {
                    margin: 1.5vh 0;
                }
            }

            &-image {
                width: 100%;
                margin: 0 auto 5vh;
            }

            &-images {
                width: 80%;
                margin: 5vh auto;
                display: flex;
                justify-content: space-around;

                img {
                    width: 33.3333%;
                }
            }
        }

        &-customers {
            img {
                width: 80%;
                margin: 0 auto;
            }
        }

        &-result {
            ul {
                width: 50%;
                margin: 3vh auto;
                list-style: none;
                
                li {
                    margin: 3vh 0;
                    padding: 0;
                    text-align: center;
                    text-align: left;
                }
            }
        }
    }
}