@import '../../App.scss';

.App-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    @include mobile {
        height: 12vh;
    }
    @include pc {
        height: 20vh;
    }
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-logo {
      @include mobile {
        width: 35%;
      }
      @include pc {
        width: 15%;
      }
      height: 7vh;
      margin: 0 5%;
      cursor: pointer;
    }
}

.App-menu {
    position: absolute;
    @include mobile {
        width: 6%;
        right: 7%;
    }
    @include pc {
        width: 2%;
        right: 6%;
    }
    height: 4vh;
    margin: 1.5vh 0;
    display: inline-block;
    transition: all 0.4s;
    box-sizing: border-box;
    cursor: pointer;

    p {
        width: 100%;
        color: #220f64;
        text-align: center;
        position: absolute;
        box-sizing: border-box;
        top: 40%;
        font-weight: bold;
    }

    span {
        position: absolute;
        height: 0.6vh;
        background-color: #220f64;
        border-radius: 4px;
        transition: all 0.4s;
        box-sizing: border-box;
        left: 0;

        &:nth-of-type(1) {
            top: 0;
            width: 100%;
        }

        &:nth-of-type(2) {
            top: 35%;
            width: 80%;
        }

        &:nth-of-type(3) {
            top: 70%;
            width: 100%;
        }
    }
}