@import '../../App.scss';

.App-main {
    width: 100%;
    background-color: #FFFFFF;
    height: 100vh;
    overflow: hidden;
    margin: 0;
    position: relative;
    z-index: 1;

    &-container {
        height: 320vh;
        display: flex;
        flex-direction: column;
        transition: transform 0.8s ease-in-out;
    }

    &-containes {
        height: 100vh;
        width: 100%;
        margin: 0;
        padding: 0;
    }

    @include pc {
        &-anchor {
            position: fixed;
            top: 50%;
            right: 2%;
            transform: translateY(-50%);
            z-index: 100;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 7vw;
            $circle-size: 20px;
    
            div {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
                color: #000000;
    
                .label_area {
                    flex: 1;
    
                    .label {
                        width: 100%;
                        opacity: 0;
                        cursor: pointer;
                        text-align: left;
    
                        &.show {
                            opacity: 1;     
                            color: #999999;
                            font-weight: 700;
                        }
                    }
                }
    
                .circle_area {
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
    
                    .circle  {
                        width: $circle-size;
                        height: $circle-size;
                        border: 5px solid #999999;
                        position: relative;
                        border-radius: 50%;
                        cursor: pointer;
                    }
            
                    .active {
                        width: $circle-size * 3;
                        height: $circle-size * 3;
                        background-color: #f32b2b;
                        border: none;
                        position: relative;
                        border-radius: 50%;
                        float: left;
                    }
                }
            }
        }
    
    }

    &-page {
      height: 100vh;
      width: 100%;
      background-image: url('../../asset/image/irehsystemcompany.jpg');
      background-position: center;
      background-size: cover;
      position: relative;

      &-content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 80%;

        &-title {
          @include mobile {
            font-size: 300% !important;
          }
          @include pc {
            font-size: 500% !important;
          }
          
          color: #018AD0 !important;
        }

        p {
          width: 80%;
          margin: 0 auto;
          font-weight: bold;
          color: #FFFFFF;
          @include mobile {
            font-size: 150%;
          }
          @include pc {
            font-size: 200%;
          }
          text-shadow: 5px 5px 5px #000000;
        }
      }
    }

    &-left {
      background-color: #afffc7;
      color: #292828;
      position: relative;

      @include mobile {
        width: 100%;
        height: 40vh;
      }
      @include pc {
        float: left;
        width: 35%;
        height: 100vh;
      }

      &-content {
        position: absolute;
        left: 50%;
        @include mobile {
            top: 60%;
        }
        @include pc {
            top: 50%;
        }
        
        transform: translate(-50%, -50%);
        width: 80%;
        font-size: 100%;
        text-align: left;

        &-title {
          @include mobile {
            font-size: 200%;
            margin: 0.5vh 0 !important;
          }
          @include pc {
            font-size: 250%;
            margin: 1vh 0 !important;
          }
          font-weight: bold;
        }

        p {
          margin: 0;
        }

        &-button {
          background-color: #672bf3;
          text-align: center;
          padding: 2% 1%;
          color: #FFFFFF;
          cursor: pointer;

          @include mobile {
            width: 40%;
            margin: 1vh 0 0!important;
          }
          @include pc {
            width: 30%;
            margin: 1vh 0 !important;
          }

          &:hover {
            background-color: inherit;
            border: 1px solid #000000;
            color: #000000;
          }
        }
      }
    }

    &-right {
      background-color: #FFFFFF;
      display: flex;
      align-items: center;

      @include mobile {
        width: 100%;
        height: 60vh;
      }
      @include pc {
        float: left;
        width: 65%;
        height: 100vh;
      }
    }

    &-box {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 10%;
      margin: 0 auto;

      @include mobile {
        width: 90%;
      }
      @include pc {
        width: 70%;
      }

      p {
        text-align: left;
        margin: 0;
      }

      &-title {
        font-weight: bold;
        font-size: 120%;
      }

      div:hover {
        cursor: pointer;
        transform: scale(1.1, 1.1);
        transition-duration: 0.5s;
      }
    }

    &-process {
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: #303030;

      &-context {
        font-size: 150% !important;
        font-weight: normal !important;
      }

      &-buttons {
        width: 100%;
        justify-content: center;
        height: 10vh !important;

        p {
          display: inline-block;
          color: #FFFFFF;
          width: 5%;
          @include pc {
            font-size: 200%;
          }
  
          @include mobile {
            font-size: 120%;
          }
          
          text-align: center;
          position: relative;
  
          &:first-of-type {
            cursor: pointer;
          }
  
          &:last-of-type {
            cursor: pointer;
          }
        }
      }

      &-area {
        width: 100%;
        height: 50vh;
        display: flex;
        justify-content: center;
        margin: 10vh 0 0;

        &-box {
          height: 50vh;
          width: 50%;
          margin: 0 auto;  
          background-color: #eaeaea;
          float: left;
          border-radius: 20px;

          img {
            width: 40%;
            height: 50vh;
            float: left;
          }

          div {
            width: 60%;
            height: 50vh;
            float: left;
            display: flex;
            flex-direction: column;
            justify-content: center;

            p {
              color: #000000;
              width: 100%;
              font-size: 220%;
              font-weight: bold;
            }
          }
        }
      }
    }
  }
