@mixin mobile {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin pc {
  @media (min-width: 768px) {
    @content;
  }
}

.App {
  text-align: center;
  word-break: keep-all;
  width: 100%;
  height: 100vh;

  &-image {
    width: 100%;
    @include mobile {
      height: 150px;
    }
    @include pc {
      height: 200px;
    }
  }
}
