@import '../../App.scss';

.App-right-menu-open {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 15;
    width: 100%;
    height: 100vh;
    transform: translateX(0);
    transition: transform 1s ease;

    &-other {
        @include mobile {
            width: 40%;
        }
        @include pc {
            width: 60%;
        }
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.7);
        float: left;
    }


    &-content {
        @include mobile {
            width: 60%;
        }
        @include pc {
            width: 40%;
        }
        height: 100vh;
        background-color: #FFFFFF;
        float: left;

        &-top-area {
            width: 90%;
            margin: 0 auto;
            padding: 2vh 0;
            border-bottom: 2px solid #E00312;

            img {
                width: 40%;
                margin: 0;
                margin-top: 5vh;
                padding: 0; 
                cursor: pointer;
            }
        }
        

        &-area {
            width: 90%;
            margin: 0 auto;
            padding: 2vh 0;
            border-bottom: 1px solid #c2c2c2;

            &-title {
                text-align: left;
                font-weight: bold;
                font-size: 180%;
                margin: 2vh 2vw;
            }

            ul {
                display: flex;
                flex-wrap: wrap;
                margin: 0 2vw;
                padding: 0;

                li {
                    list-style: none;
                    flex: 1;
                    font-size: 100%;
                    text-align: left;
                    margin: 0;
                    padding: 0;
                    cursor: pointer;

                    &:hover {
                        color: #078AD2;
                        font-weight: bold;
                    }
                }
            }
        }
    }
}

.App-right-menu {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 15;
    width: 100%;
    height: 100vh;
    transform: translateX(100%);
    transition: transform 1s ease;

    &-other {
        @include mobile {
            width: 30%;
        }
        @include pc {
            width: 60%;
        }
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.7);
        float: left;
    }

    &-content {
        @include mobile {
            width: 70%;
        }
        @include pc {
            width: 40%;
        }
        height: 100vh;
        background-color: #FFFFFF;
        float: left;

        &-top-area {
            width: 90%;
            margin: 0 auto;
            padding: 2vh 0;
            border-bottom: 2px solid #E00312;

            img {
                width: 40%;
                margin: 0;
                margin-top: 5vh;
                padding: 0; 
            cursor: pointer;
            }
        }

        &-area {
            width: 90%;
            margin: 0 auto;
            padding: 2vh 0;
            border-bottom: 1px solid #c2c2c2;

            &-title {
                text-align: left;
                font-weight: bold;
                font-size: 180%;
                margin: 2vh 2vw;
            }

            ul {
                margin: 0 2vw;
                padding: 0;
                list-style: none;
                text-align: left;

                li {
                    display: inline-block;
                    font-size: 100%;
                    text-align: left;
                    margin: 0 0 1vh;
                    margin-right: 5%;
                    padding: 0;
                    cursor: pointer;
                    color: #676767;

                    &:hover {
                        color: #078AD2;
                        font-weight: bold;
                    }
                }
            }
        }
    }
}

.App-menu-open {
    position: absolute;
    @include mobile {
        width: 8%;
        right: 5%;
    }
    @include pc {
        width: 3%;
        right: 5%;
    }
    height: 4vh;
    margin: 6vh 0;
    display: inline-block;
    transition: all 0.4s;
    box-sizing: border-box;
    cursor: pointer;

    span {
        position: absolute;
        width: 100%;
        height: 0.5vh;
        background-color: #797979;
        border-radius: 4px;
        transition: all 0.4s;
        box-sizing: border-box;

        &:nth-of-type(1) {
            top: 50%;
            transform: rotate(-45deg);
        }

        &:nth-of-type(2) {
            opacity: 0;
        }

        &:nth-of-type(3) {
            top: 50%;
            transform: rotate(45deg);
        }
    }
}